import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import '../sass/main.scss';


function Units() {

    const [allsn, setAllsn] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get('/general-test-data')
                .then((response) => {
                    // console.log(response.data);
                    const newData = response.data;
                    const allsnArr = []
                    newData.forEach((item) => {
                        // console.log(allsn.includes(item['unit_SN']));
                        if (!allsnArr.includes(item['unit_SN'])) {
                            // console.log(`number ${item['unit_SN']} in`)
                            allsnArr.push(item['unit_SN']);
                            
                        }
                        
                    });
                    allsnArr.sort();
                    setAllsn(allsnArr);
                    // console.log("allsn")
                    // console.log(allsn);
                }, (error) => {
                    console.log(error);
                });
        }
        fetchData();
    });

    const options = ["all units" ];
    
    options.push(...allsn);
    
    // console.log("options");
    // console.log(options);
    // console.log(allsn)
    const [snfrom, setSnfrom] = useState();
    const [snto, setSnto] = useState();

    const history = useHistory();
    const formData = history.location.state;
    // console.log(formData);

    function handleChange(e) {
        const selectedSN = e.target.value;
        const fromTo = e.target.name;
        // console.log(selectedSN);
        if (selectedSN) {
            if (fromTo === "from") {
                setSnfrom(selectedSN);
            }
            else if (fromTo === "to") {
                setSnto(selectedSN);
            }
        }
    }

    function handleClick(e) {

        e.preventDefault()
            history.push({
            pathname: '/tests-result',
                state: {
                    snFrom: snfrom,
                    snTo: snto,
                    testsToPresent: formData
                }
        }); 
    }

    return (
        <div>
            <h1 className="units-heading"> Choose Serial Number: </h1>
            <div className="row search-units">
                <div className="col-1-of-3 search-units--from">
                    <h3> From: </h3>
                    <div className="select-container--main">
                        <select onChange={handleChange}
                            value={snfrom}
                            name="from"
                            className="select-container--options">
                            {options.length === 1 ?
                                <option value="empty">Empty</option> :
                                options.map((Item, i) => (
                                <option key={i} value={Item}>{Item}</option>
                            ))}
                        </select>
                    </div>
                </div>
                <div className="col-1-of-3">
                    <h3>To :</h3>
                    <div className="select-container--main">
                        <select onChange={handleChange}
                            value={snto}
                            name="to"
                            className="select-container--options">
                            {options.length === 1 ?
                                <option value="empty">Empty</option> :
                                options.map((Item, i) => (
                                <option key={i} value={Item}>{Item}</option>
                            ))}
                        </select>
                    </div>
                </div>
            
                <div className="col-1-of-3">                    
                    <button type="button" onClick={handleClick}
                        className="search-button choose-btn">
                        Search
                    </button>          
                </div>
            </div>
        </div>
    )
}

export default Units;
