import React, { useEffect, useState } from 'react';
import axios from 'axios';
import '../../sass/main.scss';


function GeneralTestDataRes({resultId}) {
    const [general, setGeneral] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get('/general-test-data/findbyid/' + resultId)
                .then((response) => {
                    // console.log(response.data);
                    setGeneral(response.data);
                    // console.log(response.data['_id']);
                    
                }, (error) => {
                    console.log(error);
                });
            
        }
        fetchData();
    }, [resultId]);


    return (
        <div  className="ambient-temp-div" >
            <h3>general test details:</h3>
            <div className="general-test-details-div">
            <table className="general-table">
                <thead>
                    <tr>
                        <th>unit serial number:</th>
                        <th>test id:</th>
                        <th>Tester name: </th>
                        <th>Test date:</th>
                        <th>Final test result:</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{general['unit_SN']}</td>
                        <td>{general._id}</td>
                        <td>{general['Tester name']}</td>
                        <td>{general['Test Date']}</td>
                        <td>{general.final_test_result}</td>
                    </tr>
                </tbody>
            </table>
                
            </div>
            
        </div>
    );
};

export default GeneralTestDataRes;
